@font-face {
  font-family: "Glyphworld-Meadow";
  src: url("./fonts/GlyphWorld/GlyphWorld-Meadow.otf");
}

@font-face {
  font-family: "Glyphworld-Airland";
  src: url("./fonts/GlyphWorld/GlyphWorld-AirLand.otf");
}

@font-face {
  font-family: "Glyphworld-Mountain";
  src: url("./fonts/GlyphWorld/GlyphWorld-Mountain.otf");
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
}

h1 {
  font-size: 3rem;
  font-family: "Glyphworld-Meadow", sans-serif;
}

h2,
h3 {
  font-family: "Glyphworld-Airland", sans-serif;
  font-weight: normal;
  color: white;
}

h2 {
  font-size: 1.8rem;
}

.not-found-h2 {
  font-size: 1.2rem;
  margin-top: 7rem;
  margin-bottom: 0;
}

h3 {
  font-style: italic;
}

a {
  color: rgb(255, 255, 255);
  text-decoration: none;
}

.buy-tickets,
.home-redirect {
  color: white;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: none;
  border-radius: 2px;
}

.buy-tickets {
  font-family: "Glyphworld-Mountain", sans-serif;
  width: 36.5rem;
  font-size: 3rem;
  margin-top: 5rem;
  padding: 2rem;
  animation: blink 0.2s linear infinite alternate-reverse;
}

.home-redirect {
  font-family: "Glyphworld-Airland", sans-serif;
  font-size: 1.2rem;
  font-style: italic;
  padding: 1.5rem 2.5rem;
}

.buy-tickets:hover,
.home-redirect:hover {
  background: rgba(255, 255, 255, 0.1);
}

@keyframes blink {
  0% {
    color: white;
  }

  100% {
    color: #5f7eff;
  }
}

.image-container,
.image-container-1,
.image-container-5,
.image-container-4 {
  height: 30rem;
  width: 40rem;
  margin: 3rem auto 4rem auto;
  border: 5px solid white;
  border-radius: 2px;
}

img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
}

.image-container-1 img {
  object-position: top;
}

.image-container-5 {
  height: 20rem;
  margin-top: 2rem;
}

.image-container-4 {
  margin-bottom: 0;
}

nav {
  display: flex;
  justify-content: center;
}

.instrument-links {
  display: block;
  font-family: "Glyphworld-Airland", sans-serif;
  font-size: 1.2rem;
  margin: 0 1.5rem;
}

.instrument-links:hover {
  animation: shake 0.2s linear;
}

@keyframes shake {
  0% {
    transform: translate(-2px);
  }

  20% {
    transform: translate(2px);
  }

  40% {
    transform: translate(-2px);
  }

  60% {
    transform: translate(2px);
  }

  80% {
    transform: translate(-2px);
  }

  100% {
    transform: translate(2px);
  }
}

.on-tour {
  font-family: "Glyphworld-Airland", sans-serif;
  font-size: 1.2rem;
  color: white;
  margin: 2rem 0 4rem 0;
}

/* ----------------------------MEDIA QUERIES---------------------------- */

/* @media (max-height:1904px) {
  .row-1-boxes {
      margin-left: 20rem;
  }
} */

@media (min-height:400px) and (max-height:900px) {
  .buy-tickets {
    font-size: 2rem;
    width: 50rem;
    padding: 1rem;
  }
  .image-container,
  .image-container-1,
  .image-container-5,
  .image-container-4 {
    height: 25rem;
  }
  .image-container-4 {
    margin: 3rem auto 2rem auto;
  }
  .image-container-4 img {
    object-position: 0 -150px;
  }
  .image-container-5 {
    margin-bottom: 2rem;
  }
  .on-tour {
    margin: 2rem 0 2rem 0;
  }
  .not-found-h2 {
    margin-top: 2rem;
  }
}